body,
html,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Public Sans', sans-serif;
  font-size: 10pt;
  color: white;
}

::-webkit-scrollbar {
  display: none;
}

* {
  -webkit-overflow-scrolling: touch;
}

a {
  font-weight: bold;
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.container {
  height: 100%;
  width: 100%;
  overflow: scroll;
  background: black;
}

.content {
  margin: 0 auto;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.content-anchor {
  position: relative;
  width: 460px;
  height: 500px;
}

.content-scrollable {
  overflow: scroll;
  height: 500px;
  /* overflow x cant be visible if overflow y is scroll */
  padding: 150px;
  margin: -150px;
}

.left-bar {
  position: absolute;
  left: -250px;
  -webkit-transform: rotate(-90deg) translateY(-50%);
      -ms-transform: rotate(-90deg) translateY(-50%);
          transform: rotate(-90deg) translateY(-50%);
  top: 50%;
  letter-spacing: 3px;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

nav > a {
  margin: 8px;
}

.right-bar {
  position: absolute;
  right: -150px;
  -webkit-transform: rotate(90deg) translateY(-50%);
      -ms-transform: rotate(90deg) translateY(-50%);
          transform: rotate(90deg) translateY(-50%);
  top: 50%;
  letter-spacing: 5px;
  text-transform: uppercase;
}

.bottom-bar {
  position: absolute;
  bottom: -150px;
  right: 100px;
  font-size: 12px;
  letter-spacing: 2px;
}
.top-bar {
  position: absolute;
  top: -150px;
  left: 100px;
}

.landing {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}
.landing img {
  width: 460px;
  /*height: 360px;*/
  -o-object-fit: cover;
     object-fit: cover;
  margin-bottom: 24px;
}

.landing h3 {
  font-weight: 800;
  letter-spacing: 5px;
  font-size: 96px;
  color: #ff3500;
  position: absolute;
  top: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  white-space: nowrap;
  text-transform: uppercase;
}

.landing h3::before {
  content: attr(data-text);
  position: absolute;
  top: -35%;
  -webkit-clip-path: inset(0 0 52% 0);
          clip-path: inset(0 0 52% 0);
  text-transform: uppercase;
}

.landing h3.bottom {
  bottom: 20%;
  top: initial;
}

.landing h3.bottom::before {
  bottom: -25%;
  top: initial;
  -webkit-clip-path: inset(65% 0 0 0);
          clip-path: inset(65% 0 0 0);
}

.landing .cover {
  position: relative;
}

.print {
  /* SinglePhotoGallery's parent needs a defined height */
  height: 100%;
}

.about {
}

@media only screen and (max-width: 768px) {
  /* responsive here */
  .content {
    height: auto;
  }
  .content-anchor {
    width: auto;
    height: auto;
  }

  .content-scrollable {
    height: auto;
    padding: 84px 60px;
    margin: 0;
  }

  .top-bar,
  .left-bar,
  .bottom-bar,
  .right-bar {
    position: fixed;
  }
  .top-bar {
    top: 10px;
  }
  .bottom-bar {
    bottom: 10px;
  }
  .left-bar {
    left: -100px;
  }
  .right-bar {
    right: 10px;
  }

  .landing h3 {
    font-size: 36px;
  }
}



